import React,{useState} from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import authService from "./services/authServices";
import Link from '@material-ui/core/Link';


function DialogFileUploadGlobal(props) {
    const [filesGlobal, setFilesGlobal] = useState("");
    const [errorJSONGlobal, setErrorJSONGlobal] = useState(false);


    function isJsonString(str) {
        try {
            JSON.parse(str);
        } catch (e) {
            return false;
        }
        return true;
    }
    
    
    const handleChange = (e) => {
        const fileReader = new FileReader();
        fileReader.readAsText(e.target.filesGlobal[0], "UTF-8");
        fileReader.fileName = e.target.filesGlobal[0].name;
        
        fileReader.onload = e => {
            if(isJsonString(e.target.result)){
                setFilesGlobal(e.target.result);
                authService.saveJSON(1, e.target.result, e.target.fileName);
                setErrorJSONGlobal(false);

            }else{
                setErrorJSONGlobal(true);
            }
        };

    }   
  return (
    <div>
         <Dialog
            open={props.c_open}
            onClose={props.close}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">{"Upload Reactions File Global"}</DialogTitle>
            <DialogContent>
            
                <div className="container-user">
                    
                    <div className="item-user">
                     
                        <DialogContentText id="alert-dialog-description">
                            {"ADMIN Please upload your reactions file ("}
                            <Link
                                component="button"
                                variant="body2"
                                onClick={() => {
                                    window.open("http://localhost:3000/data/reactionsGlobal.json", "_blank");
                                }}
                                >
                                format JSON
                            </Link>
                            {"): " }
                            <br />
                            <input type="file" onChange={handleChange} /><br />
                            <br />
                            <p>Filename uploaded: {authService.getJSONFilename(1)===null ? "No file uploaded" :authService.getJSONFilename(1) }</p><br />
                            {"New File content: " }<br />
                            {errorJSONGlobal ? <p style={{color:'red'}} >Error in JSON format, please review file</p>: ""}
                            {filesGlobal}
                            
                        </DialogContentText>
                        
                       
                    </div>
                </div>
            </DialogContent>
            <DialogActions>
            <Button color="primary">
                Cancel
            </Button>
            <Button onClick={props.close} color="primary" autoFocus disabled={errorJSONGlobal ? true: false}>
                Finish
            </Button>
            </DialogActions>
        </Dialog>
    </div>
  )
}

export default DialogFileUploadGlobal