import React,{useState} from 'react'
import {Link} from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import { grey } from '@material-ui/core/colors';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import IconButton from '@material-ui/core/IconButton';
import AuthService from "./services/authServices";
import { useHistory } from "react-router-dom";
import './NavBar.css'
import DialogComponent from './DialogComponent';
import DialogFileUpload from './DialogFileUpload';
import DialogFileUploadGlobal from './DialogFileUploadGlobal';


const useStyles = makeStyles((theme) => ({
    orange: {
      color: theme.palette.getContrastText("#2583A8"),
      backgroundColor: "#2583A8",
    },
  }));
 
function checkRole (data){
    if(data.role==="ADMIN"){
        return true;
    }else{
        return false;
    }
}

function NavBar() {
    const [click, setClick] = useState(false);
    const [showUser, setShowUser] = useState(false);
    const [showSettings, setShowSettings] = useState(false);
    const [showGlobal, setShowGlobal] = useState(false);
    const handleClick = () => setClick(!click);
    const closeSmallMenu = () => setClick(false);
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const classes = useStyles();
    const history = useHistory();
    const url = '';
    //const { userRole, errorUR, isLoadingUR } = FetchService.useFetchUserRole();
    

    const handleMenu = (event) => {
        setAnchorEl(event.currentTarget);
      };
    
    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleAccount = () => {
        setAnchorEl(null);
        setShowUser(true);
    };

    const handleSettings = () => {
        setAnchorEl(null);
        setShowSettings(true);
    
    }

    const handleGlobal = () => {
        setAnchorEl(null);
        setShowGlobal(true);
    
    }

    const handleCloseSettings = (event, reason) => {
        if(reason !== "backdropClick" && AuthService.isFileLoadedJSON(0) ){
            setShowSettings(false);
            window.location.reload(false);
        }
    
    };

    const handleCloseGlobal = (event, reason) => {
        if(reason !== "backdropClick" && AuthService.isFileLoadedJSON(1) ){
            setShowGlobal(false);
            window.location.reload(false);
        }
    
    };

    const handleCloseUser = () => {
        setShowUser(false);
    };

    const handleLogOut = () => {
        setAnchorEl(null);
        AuthService.logout();
        history.push({
            pathname: '/NewsCollector/',
            //state: { pvalue: values.username } your data array of objects
          });
    };

    /*
    let adminF;
    if(!isLoadingUR ){
        adminF = checkRole(userRole);
    }
    */



    return (
        <div>
            <nav className="navbar">
                <div className="navbar-container">
                    <Link to="/profile" className="navbar-logo">                     
                        <i class="fas fa-dice-d20 fa-2x"  />
                        <div className="navbar-name">
                            <b>News Collector</b>
                        </div>
                    </Link>
                    <div className="menu-icon" onClick={handleClick}>
                        <i className={click ? 'fas fa-times' : 'fas fa-bars fa-sm'}/>
                    </div>
                    <ul className={click ? 'nav-menu active': 'nav-menu'}>
                        <li className="nav-item">
                            <Link to='/profile' className="nav-links" onClick={closeSmallMenu}>
                            Your Profile Results
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link to='/topnews/0' className="nav-links" onClick={closeSmallMenu}>
                            Top News Sources
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link to='/comparison' className="nav-links" onClick={closeSmallMenu}>
                            Global Stats
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link to='/about' className="nav-links" onClick={closeSmallMenu}>
                            About
                            </Link>
                        </li>
                      
                       
                    </ul>
                    <div className='login-nav'>
                        <IconButton
                            size="medium"
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            aria-haspopup="true"
                            onClick={handleMenu}
                            color="inherit"
                        >
                            <Avatar className={classes.orange}>{AuthService.getName().charAt(0)}</Avatar>
                            <ArrowDropDownIcon style={{ color: grey[50] }}></ArrowDropDownIcon>
                            
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={anchorEl}
                            anchorOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                            }}
                            keepMounted
                            transformOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                            }}
                            open={Boolean(anchorEl)}
                            onClose={handleClose}
                        >
                            <MenuItem onClick={handleAccount}>My Account</MenuItem>
                            <MenuItem onClick={handleSettings}>Update Reactions Data</MenuItem>
                            { /*
                            adminF ? 
                                <MenuItem onClick={handleGlobal}>ADMIN GLOBAL</MenuItem>
                              : ""
                              */
                            }
                            <MenuItem onClick={handleLogOut}>Log Out</MenuItem>
                        </Menu>

                    </div>
                    <DialogComponent
                        c_open = {showUser}
                        close = {handleCloseUser} >

                    </DialogComponent>
                    <DialogFileUpload  
                        c_open = {showSettings}
                        close = {handleCloseSettings}/>
                        
                    <DialogFileUploadGlobal  
                        c_open = {showGlobal}
                        close = {handleCloseGlobal}/>
                        
                </div>

            </nav>
            
        </div>
    )
}

export default NavBar
